	module.exports = {"data":{"markdownRemark":{"html":"<h2>Ems and Ens</h2>\n<p>Ems and Ens are a form of the punctuation mark called ‘dash’. Although similar in appearance to a hyphen, they serve different purposes.</p>\n<h3>Em Dash</h3>\n<p>Use an Em dash to denote a break in the sentence. Substitute it for a comma or to denote a pause in a sentence. They are also used to attribute a quote to a speaker.\nAn Em dash is one em wide—the width of a point size of a typeface. Don’t put any spaces before and after an em dash.\n<br>\nFor example: The noise from the neighbor’s house—it’s killing me.</p>\n<ul>\n<li>Command for an Em dash on a mac : Shift-Option-Hyphen</li>\n<li>Command for an Em dash on Microsoft Word  :  Alt + Ctrl + (minus)</li>\n<li>Em dash in HTML : <code>&#x26;mdash;</code> or <code>&#x26;#8212;</code></li>\n</ul>\n<h3>En Dash</h3>\n<p>Use an En dash as a replacement for the word ‘to’ or to denote a range of numbers. An En dash is half the width of an Em dash. Don’t put any spaces before and after an en dash.\n<br>\nFor example: The first world war lasted from 1914–1918.</p>\n<ul>\n<li>Command for an En dash on a mac : Option-Hyphen</li>\n<li>Command for an En dash on Microsoft Word  :  Ctrl + (minus)</li>\n<li>En dash in HTML : <code>&#x26;ndash;</code> or <code>&#x26;#8211;</code></li>\n</ul>\n<h4>More Information:</h4>\n<p>Dash - <a href='https://en.wikipedia.org/wiki/Dash#Em_dash' target='_blank' rel='nofollow'>Wikipedia</a></p>\n<p>Thinking With Type by Ellen Lupton - <a href='http://thinkingwithtype.com' target='_blank' rel='nofollow'>Thinking With Type</a></p>\n<p>Modularscale - <a href='http://www.modularscale.com/' target='_blank' rel='nofollow'>http://www.modularscale.com/</a></p>","fields":{"slug":"/typography/ems-and-ens/"},"frontmatter":{"title":"Ems and Ens"}}},"pathContext":{"slug":"/typography/ems-and-ens/"}}

